.news-single {
    padding: 40px 0 120px;

    @media screen and (max-width: 1029.98px) {
        padding: 20px 0 60px;
    }

    .container {

    }
}

.news-single__image {
    width: 100%;
    height: max-content;
    margin-bottom: 45px;
    border-radius: var(--radius-2);

    img {
        width: 100%;
        height: 100%;
        border-radius: var(--radius-2);
    }
}

.news-single__title {
    margin-bottom: 25px;
}

.news-single__content {
    ul, ol {
        ul, ol {
            padding-left: 40px;
        }
    }

    ul {
        list-style: disc inside;
    }

    ol {
        list-style: decimal inside;
    }

    li {
        display: list-item !important;
    }

    img {
        border-radius: var(--radius-5) !important;
    }
}

.news-single {
    .container {

        .news-single__content {
